import useGlobalStore from '@/store/common/global'
import useActiveOrdersStore from '@/store/profile/activeOrders'
import { isShowAssets } from '@/utils/profile'
import { AccountSwitch, CetusTooltip } from '@cetus/design'
import { useAccountStore } from '@cetus/stores'
import { Icon, NoData } from '@cetus/ui-kit'
import { formatCurrency } from '@cetus/utils'
import { Center, Flex, HStack, Skeleton, Spinner, Text, useDisclosure, VStack } from '@chakra-ui/react'
import { useEffect, useState } from 'react'

type H5MainProps = {
  totalValue: string
  onRefreshTask: () => void
  isLoading: boolean
  children: React.ReactNode
}

function H5Main({ totalValue, onRefreshTask, isLoading, children }: H5MainProps) {
  const [isMounted, setIsMounted] = useState(false)
  const { currentAccount, onWalletModal, accounts } = useAccountStore()
  const { isShowProfileAssets, setIsShowProfileAssets } = useGlobalStore()
  const { isAutoRefresh, dcaOrderListLoading, setAutoRefreshCount, resetAutoRefreshCount } = useActiveOrdersStore()
  useEffect(() => {
    setIsMounted(true)
  }, [])
  const { onClose } = useDisclosure()
  return (
    <VStack w="100%" mt="28px">
      {!isMounted ? (
        <Flex h="300px" w="100%" justifyContent="center" alignItems="center">
          <Spinner />
        </Flex>
      ) : !currentAccount?.address ? (
        <NoData type="nowallet" mt="20px" onboard={() => onWalletModal(true)} />
      ) : (
        <VStack w="100%" gap="16px" alignItems="start">
          <HStack w="100%" h="18px" align="flex-end" justify="space-between">
            <HStack h="18px">
              <Text h="18px" lineHeight="18px" fontSize={{ base: '14px', lg: '16px' }} color="text_caption">
                Total Value
              </Text>
              <CetusTooltip
                showTooltip={false}
                placement="bottom-end"
                tooltip={<Text fontSize="12px">{isShowProfileAssets ? 'Hide Value' : 'View Value'}</Text>}
              >
                <Center>
                  <Icon
                    xlinkHref={isShowProfileAssets ? '#icon-hide_eyes' : '#icon-show_eyes'}
                    svgFill={isShowProfileAssets ? 'text_caption' : 'text_caption'}
                    // svgHover={isShowProfileAssets ? 'primary' : isApp ? 'text_paragraph' : 'text_caption'}
                    onClick={() => setIsShowProfileAssets(!isShowProfileAssets)}
                  />
                </Center>
              </CetusTooltip>
            </HStack>
            <HStack h="16px" align="center" gap="0" cursor="pointer" onClick={() => console.log('jump to transactions')}>
              <Text h="16px" lineHeight="16px" fontSize="12px">
                Transactions
              </Text>
              <Icon xlinkHref="#icon-icon_ascending" fontSize="16px" transform="rotate(90deg)" />
            </HStack>
          </HStack>
          <Skeleton isLoaded={!isLoading} h="32px">
            <Text mt="-6px" letterSpacing="1px" h="32px" fontSize="28px" lineHeight="32px" color="text_caption" fontWeight="500">
              {isShowAssets(formatCurrency(totalValue, 2), isShowProfileAssets)}
            </Text>
          </Skeleton>
          {accounts.length > 1 && (
            <HStack w="100%" h="44px" borderRadius="8px" bg="account_switch_bg" backdropFilter="blur(4px)" p="12px 12px 12px 0">
              <Icon xlinkHref="#icon-account" svgFill="text_caption" />
              <Text color="text_caption" fontSize="14px" lineHeight="14px">
                Roul :{' '}
              </Text>
              <AccountSwitch currentAccount={currentAccount} accounts={accounts} onClose={onClose} isProfile={true} />
            </HStack>
          )}
          {children}
        </VStack>
      )}
    </VStack>
  )
}

export default H5Main
